import React from 'react';

class Prompt extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            visible: false
        }
    }

    showPrompt () {
        this.setState({visible: true});

    }

    

    render () {
        return (
            <div className={['plug-prompt', this.state.visible ? 'active' : ''].join(' ')}>
                <div className="content">
                    {'删除成功'}
                </div>

                <style jsx>{`
                    .plug-prompt {
                        position: fixed;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                        background: #666;
                        padding: 20px 30px;
                        font-size: 14px;
                        color: #fff;
                        border-radius: 6px;
                        opacity: 0;
                        width: 0;
                        height: 0;
                    }
                    .plug-prompt.active {
                        opacity: 1;
                        width: auto;
                        height: auto;
                    }
                `}</style>
            </div>
        )
    }
}
export default Prompt;
